@charset "UTF-8";
/* --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 * 共通項目
 * --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 */
* {
	margin: 0;
	padding: 0;
	line-height: 1.2;
}

html {
	font-size: 14px;
}

body {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	font-family: "メイリオ", "Meiryo", sans-serif;
	font-size: 1rem;
	color: #FFFFFF;
	background-color: #000011;
}

@media only screen and (max-width: 992px) {
	body {
		display: flex;
		flex-direction: column;
		justify-content: unset;
		align-items: unset;
	}
}

@media print {
	body {
		color: #000000;
		background-color: unset;
	}
}

@media print {
	body > header {
		display: none;
	}
}

body > input[type="checkbox"] {
	display: none;
}

@media only screen and (max-width: 992px) {
	body > input[type="checkbox"].panel-switch:not(:checked) + nav {
		overflow: hidden;
		height: 0;
		padding: 0;
		border-width: 0;
	}
}

body > .content {
	display: block;
	width: calc(100% - 240px);
	margin-left: 240px;
}

@media only screen and (max-width: 992px) {
	body > .content {
		width: 100%;
		margin-left: 0;
	}
}

@media print {
	body > .content {
		width: 100%;
		margin-left: 0;
	}
}

a {
	transition: all 0.25s ease-out;
	text-decoration: none;
	color: #9999FF;
}

a:hover {
	color: #FFCC66;
}

h1, h2, h3, h4, h5, h6 {
	font-family: "游ゴシック体", "Yu Gothic", "YuGothic", "ヒラギノ角ゴシック Pro", "Hiragino Kaku Gothic Pro", "メイリオ", "Meiryo", "Osaka", "ＭＳ Ｐゴシック", "MS PGothic", sans-serif;
}

dl dd {
	margin-left: 2rem;
}

ul {
	list-style: none;
}

ul li:not(:first-child) {
	margin-top: 5px;
}

ul ul {
	margin-left: 2rem;
}

ol {
	margin-left: 2rem;
}

ol li:not(:first-child) {
	margin-top: 5px;
}

@media only screen and (max-width: 992px) {
	.md-hide {
		display: none;
	}
}

@media only screen and (min-width: 993px) {
	.md-show {
		display: none;
	}
}

.mintyo {
	font-family: "游明朝", YuMincho, "ヒラギノ明朝 ProN W3", "Hiragino Mincho ProN", "HG明朝E", "ＭＳ Ｐ明朝", "ＭＳ 明朝", serif;
}

.stub, .deprecated {
	margin: 10px;
	padding: 20px;
	border-width: 5px;
	border-style: solid;
}

@media print {
	.stub, .deprecated {
		color: #FFFFFF;
	}
}

.stub {
	border-image: linear-gradient(to top right, #1164FF, #000011, #1164FF) 1/5px 5px/0 stretch;
	background-color: #000033;
}

.deprecated {
	border-image: linear-gradient(to top right, #1164FF, #FF0D0D, #1164FF) 1/5px 5px/0 stretch;
	background-color: #330000;
}

::-webkit-scrollbar {
	width: 10px;
	height: 10px;
}

::-webkit-scrollbar-track {
	background-color: transparent;
}

::-webkit-scrollbar-thumb {
	transition: all 0.1s ease;
	border-radius: 10px;
	background-image: linear-gradient(to bottom right, rgba(255, 255, 255, 0.75), rgba(255, 255, 255, 0.25));
	box-shadow: 0 0 0 1px rgba(255, 255, 255, 0.3);
}

@keyframes flashString {
	0% {
		color: #FFFFFF;
		text-shadow: 0 0 0 rgba(255, 255, 255, 0);
	}
	90% {
		color: #FFFFFF;
		text-shadow: 0 0 0 rgba(255, 255, 255, 0);
	}
	95% {
		color: #FFFF66;
		text-shadow: 0 0 10px #FFFF66;
	}
	100% {
		color: #FFFFFF;
		text-shadow: 0 0 0 rgba(255, 255, 255, 0);
	}
}

/* --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 * Prism Overwrite
 * --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 */
code[class*="language-"], pre[class*="language-"] {
	margin: 10px 0;
	tab-size: 4;
	font-family: "Migu 1M", Consolas, Monaco, "Andale Mono", "Ubuntu Mono", monospace;
	line-height: 1.2;

	-webkit-overflow-scrolling: touch;
}

@media only screen and (max-width: 576px) {
	code[class*="language-"], pre[class*="language-"] {
		max-height: 80vh;
	}
}

code[class*="language-"]::-webkit-scrollbar, pre[class*="language-"]::-webkit-scrollbar {
	width: 5px;
	height: 5px;
}

@media print {
	.line-highlight {
		display: none;
	}
}

/* --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 * サイドバー
 * --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 */
.sidebar {
	position: fixed;
	top: 0;
	left: 0;
	overflow-y: scroll;
	box-sizing: border-box;
	width: 240px;
	min-width: 240px;
	height: 100vh;
	min-height: 100vh;
	padding: 10px 0 2rem;
	border-width: 0;
	border-style: solid;
	border-right-width: 3px;
	border-image: linear-gradient(to bottom, #000011, #1164FF) 1/0 3px 0 0/0 stretch;
}

@media only screen and (max-width: 992px) {
	.sidebar {
		position: static;
		width: 100%;
		height: auto;
		min-height: unset;
		margin-bottom: 20px;
		padding-left: 2px;
		transition: all 0.5s ease-out;
		border-bottom-width: 3px;
		border-left-width: 3px;
		border-image: linear-gradient(to bottom, #000011, #1164FF) 1/0 3px 5px/0 stretch;
	}
}

@media print {
	.sidebar {
		display: none;
	}
}

@media only screen and (max-width: 992px) {
	.sidebar > ul {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		padding: 0 5px;
		padding-bottom: 10px;
	}
}

.sidebar > ul > li {
	margin-top: 15px;
}

@media only screen and (max-width: 992px) {
	.sidebar > ul > li {
		margin-top: 10px;
	}
}

@media only screen and (max-width: 768px) {
	.sidebar > ul > li {
		margin-top: 5px;
	}
}

.sidebar > ul > li h3 {
	display: flex;
	align-items: center;
	padding-top: 3px;
	padding-left: 10px;
	animation: flashString 10s linear infinite;
	font-size: 1.25rem;
	border-width: 0;
	border-style: solid;
	border-top-width: 3px;
	border-image: linear-gradient(to right, #000011, #1164FF, #FFFF66) 1/3px 0 0/0 stretch;
}

@media only screen and (max-width: 992px) {
	.sidebar > ul > li h3 {
		padding-left: 0;
	}
}

.sidebar > ul > li h3 a {
	display: inline-flex;
	align-items: center;
	animation: flashString 10s linear infinite;
}

.sidebar > ul > li input[type="checkbox"] {
	display: none;
}

.sidebar > ul > li input[type="checkbox"].panel-switch:not(:checked) + ul {
	overflow: hidden;
	height: 0;
	padding: 0;
	border-width: 0;
}

.sidebar > ul > li ul {
	margin-left: calc(1em + 12px);
	padding-right: 2px;
	transition: all 0.5s ease-out;
	text-indent: -1em;
}

@media only screen and (max-width: 992px) {
	.sidebar > ul > li ul {
		margin-left: calc(1em + 2px);
	}
}

.sidebar > ul > li ul li {
	transition: all 0.5s ease-out;
}

.sidebar > ul > li ul li:before {
	padding-right: 2px;
	content: "├";
	transition: all 0.5s ease-out;
	animation: flashString 10s linear infinite;
}

.sidebar > ul > li ul li:last-child:before {
	padding-right: 2px;
	content: "└";
	transition: all 0.5s ease-out;
	animation: flashString 10s linear infinite;
}

.sidebar::-webkit-scrollbar {
	width: 5px;
	height: 5px;
}

@media only screen and (max-width: 992px) {
	.sidebar::-webkit-scrollbar {
		width: 0;
		height: 0;
	}
}

.sidebar::-webkit-scrollbar-track {
	border-radius: 10px;
	background-image: linear-gradient(to bottom, #000011, #1164FF);
}

.sidebar::-webkit-scrollbar-thumb {
	border-radius: 10px;
	background-image: linear-gradient(to bottom right, #CCCCCC, #888888);
}

.nav-button {
	display: flex;
	justify-content: space-evenly;
}

@supports (-ms-ime-align: auto) or ((-webkit-text-size-adjust: none) and (-webkit-marquee-repetition: infinite) and (object-fit: fill)) {
	.nav-button {
		justify-content: space-around;
	}
}

.nav-button button {
	width: 100px;
	height: 24px;
	font-size: 0.9rem;
	color: #FFFFFF;
	border: none;
	border-radius: 5px;
	background-color: #1164FF;
}

.expand-box {
	display: flex;
	justify-content: center;
	align-items: center;
	box-sizing: border-box;
	width: 20px;
	height: 20px;
	margin: auto 5px auto 0;
	cursor: pointer;
	font-size: 0.9rem;
	border: 1px #FFFFFF solid;
}

/* --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 * ヘッダー
 * --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 */
header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-top: 1rem;
	margin-bottom: 3px;
	border-width: 0;
	border-style: solid;
	border-bottom-width: 5px;
	border-image: linear-gradient(to right, #000011, #1164FF, #FFFF99) 1/0 0 5px/0 stretch;
}

@media print {
	header {
		margin-top: 0;
	}
}

header h1 {
	padding-left: 2.5rem;
	font-size: 2.5rem;
}

@media only screen and (max-width: 768px) {
	header h1 {
		font-size: 4.7vw;
	}
}

header label {
	display: none;
}

@media only screen and (max-width: 992px) {
	header label {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		width: 160px;
		height: 32px;
		margin-right: 5px;
		margin-bottom: 5px;
		border-radius: 5px;
		background-color: #1164FF;
	}
}

/* --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 * コンテンツ
 * --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 */
section {
	margin-top: 10px;
}

section:first-child {
	margin-top: 5px;
}

section:not(:first-child) {
	padding-top: 5px;
	border-width: 0;
	border-style: solid;
	border-top-width: 2px;
	border-image: linear-gradient(to right, #000011, #1164FF, #FFFF99) 1/2px 0 0/0 stretch;
}

section h1, section h2, section h3, section h4, section h5, section h6, section p {
	padding: 0 0.5rem;
}

section h2 {
	font-size: 1.75rem;
}

section h3 {
	margin-top: 5px;
	margin-left: 5px;
	padding-left: 5px;
	font-size: 1.25rem;
	border-left: 10px solid #1164FF;
}

section h4 {
	margin-top: 5px;
	font-size: 1rem;
}

section ul {
	margin-left: 2rem;
	list-style: disc;
}

section ul li {
	padding-right: 10px;
}

section dl {
	padding-left: 0.5rem;
}

section dl dt h1, section dl dt h2, section dl dt h3, section dl dt h4, section dl dt h5, section dl dt h6 {
	padding: 0;
}

section dl dt h3 {
	padding-left: 5px;
}

section dl dd p {
	padding: 0;
}

section dl dd ul li:first-child {
	margin-top: 5px;
}

section dl dd ul li:last-child {
	margin-bottom: 5px;
}

.object-safe {
	color: #33FF33;
}

.object-euclid {
	color: #FFFF33;
}

.object-keter {
	color: #FF3333;
}

/* --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 * フッター
 * --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 */
footer {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-top: 3px;
	padding-top: 5px;
	padding-bottom: 5px;
	border-width: 0;
	border-style: solid;
	border-top-width: 5px;
	border-bottom-width: 20px;
	border-image: linear-gradient(to right, #000011, #1164FF, #FFFF99) 1/5px 0 20px/0 stretch;
}

@media only screen and (max-width: 768px) {
	footer {
		flex-direction: column-reverse;
	}
}

@media only screen and (max-width: 768px) {
	footer p {
		box-sizing: border-box;
		width: 100%;
	}
}

footer p:first-child a {
	padding: 0 0 0 0.5rem;
	animation: flashString 10s linear infinite;
	text-decoration: line-through;
	color: unset;
}

@media print {
	footer p:first-child a {
		animation: unset;
		color: #000000;
	}
}

footer p:first-child b {
	font-weight: 900;
}

footer p:last-child {
	margin-left: 2rem;
	text-align: right;
	white-space: nowrap;
}

@media only screen and (max-width: 768px) {
	footer p:last-child {
		margin-bottom: 15px;
		margin-left: 0;
	}
}

@media only screen and (max-width: 576px) {
	footer p:last-child {
		white-space: normal;
	}
}

/* --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 * レスポンシブの設定
 * --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 */
